.join-type {
    padding: 150px 0;
}
.member-form {
    max-width: 665px;
    border: 1px solid #dedede;
    background: #fff;
    margin: 0 auto;
    padding: 50px;
}
.member-form h3 {
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 30px;
}
.member-form .list li {
    font-size: 14px;
    line-height: 1.6;
    position: relative;
    padding-left: 10px;
}
.member-form .list li::before {
    content: "";
    width: 3px;
    height: 3px;
    background: #000;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 7px;
}
.member-form h4 {
    font-weight: 500;
    font-size: 16px;
    text-decoration: underline;
}
.member-form .blue .list {
    background-color: #f5f5f5;
    padding: 20px;
}
.member-form .blue .check {
    display: block;
    margin-top: 10px;
}

.join-box label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
}
.join-box input {
    border: 1px solid #dfdfdf;
    width: 100%;
    padding: 15px;
    height: 50px;
    margin-bottom: 30px;
}
.join-box .modify input {
    background: #dfdfdf;
}
.join-box .overlap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.join-box .overlap label {
    width: 100%;
}
.join-box .overlap input {
    width: 70%;
    margin-bottom: 0;
}
.join-box .overlap .test {
    width: 26%;
    background: #f5f5f5;
    display: inline-block;
    text-align: center;
    height: 50px;
    line-height: 50px;
}
.join-box .overlap .comment {
    width: 100%;
    font-size: 14px;
    padding-top: 10px;
    color: red;
    margin-bottom: 20px;
}

.join-box .basic input {
    margin-bottom: 0;
}
.join-box .basic .comment {
    width: 100%;
    font-size: 14px;
    padding-top: 10px;
    color: red;
    margin-bottom: 20px;
}
.join-submit {
    font-size: 16px;
    background: #eee;
    padding: 15px;
    display: block;
    width: 100%;
    transition: all 0.3s;
    cursor: pointer;
}
.join-submit:hover {
    box-shadow: inset 0 0 0 2px #222;
    background: #fff;
}
